const fs = require('fs')
const path = require('path')

class Fs {
  constructor () {
  }
  getBasename (filePath) {
    if (!(typeof filePath === 'string' && filePath)) throw new Error(`[ File ][ getBasename ] The filePath (${filePath}) is invalid.`)
    return path.basename(filePath)
  }
  getExtname (filepath, filterDot = true) {
    let ext = path.extname(filepath).toLocaleLowerCase()
    if (filterDot && ext.length >= 2 && ext[0] === '.') {
      return ext.slice(1)
    }
    return ext
  }
  getFile (filePath) {
    return new Promise((resolve, reject) => {
      fs.open(filePath, 'r', (err, fd) => {
        if (err) return reject(err)
        return resolve(fd)
      })
    })
  }
  getFileSync (filePath) {
    try {
      return fs.openSync(path.resolve(filePath), 'r')
    } catch (error) {
      throw error
    }
  }
  closeFile (fd) {
    if (!(typeof fd === 'number' && fd > 0)) throw new Error(`[ File ][ closeFile ] The fd (${fd}) is invalid.`)
    fs.close(fd, err => {
      if (err) throw err
    })
  }
  isExist (filePath) {
    return new Promise((resolve, reject) => {
      fs.access(filePath, fs.constants.F_OK, (err) => {
        err ? resolve(false) : resolve(true)
      })
    })
  }
  readBytes (fd, position = 0, length = 0) {
    return new Promise((resolve, reject) => {
      let buf = Buffer.alloc(length)
      fs.read(fd, buf, 0, length, position, (err, bytesRead) => {
        if (err) return reject(err)
        if (bytesRead < length) {
          buf = buf.slice(0, bytesRead)
        }
        return resolve(buf)
      })
    })
  }
  readBytesSync (fd, position = 0, length = 0) {
    try {
      let buf = Buffer.alloc(length)
      let bytesRead = fs.readSync(fd, buf, 0, length, position)
      if (bytesRead < length) {
        buf = buf.slice(0, bytesRead)
      }
      return buf
    } catch (error) {
      throw error
    }
  }
  readChunk (filePath, position = 0, length = 0) {
    return new Promise((resolve, reject) => {
      this.getFile(filePath)
        .then(fd => {
          let buf = Buffer.alloc(length)
          fs.read(fd, buf, 0, length, position, (err, bytesRead) => {
            this.closeFile(fd)
            if (err) return reject(err)
            if (bytesRead < length) {
              buf = buf.slice(0, bytesRead)
            }
            return resolve(buf)
          })
        })
        .catch(err => { return reject(err) })
    })
  }
  readFile (filePath, encoding) {
    return new Promise((resolve, reject) => {
      let argv = [path.resolve(filePath)]
      encoding ? argv.push(encoding) : ''
      const cb = (err, data) => {
        err ? reject(err) : resolve(data)
      }
      argv.push(cb)
      fs.readFile(...argv)
    })
  }
  readFileSync (filePath, encoding) {
    try {
      return fs.readFileSync(path.resolve(filePath), encoding)
    } catch (error) {
      throw error
    }
  }
  writeFile (filePath, data) {
    return new Promise((resolve, reject) => {
      if (!(data && data.length > 0)) {
        return reject(new Error('Invalid data.'))
      }

      fs.writeFile(path.resolve(filePath), data, err => {
        if (err) {
          return reject(err)
        }
        return resolve(data.length)
      })
    })
  }
}

module.exports = Fs
